  margin-right: 30px;
}

.footer__terms a:hover:before {
  color: #a9abb0;
}

.footer__terms a:before {
  content: '|';
  position: absolute;
  right: -17px;
  top: -1px;
  pointer-events: none;
}

.footer__terms a:last-child {
  margin-right: 0;
}

.footer__terms a:last-child:before {
  display: none;
}

.burger {
  width: 30px;
  height: 30px;
  padding: 5px;
  display: none;
}

.burger.is--active span {
  background: #7f7af0;
}

@media only screen and (max-width: 768px) {
  .burger {
    display: block;
  }
}

.burger span {
  display: block;
  width: 20px;
  height: 2px;
  background: #000;
}

.burger span:nth-of-type(2) {
  margin: 5px 0;
}

.mobile__menu {
  position: fixed;
  top: 50px;
  right: 0;
  width: 200px;
  background: #fff;
  padding: 5px 30px 15px 15px;
  display: none;
  opacity: 0;
  text-align: right;
  box-shadow: 0 22px 22px rgba(0, 0, 0, 0.16);
}

.mobile__menu svg {
  display: none;
}

.mobile__menu a {
  display: block;
  color: #3e3e40;
  font-size: 12px;
}

.mobile__menu ul + a {
  margin-bottom: 15px;
}

.mobile__menu ul li {
  float: none;
  margin-right: 0;
  margin-bottom: 15px;
}

.mobile__menu ul li:before {
  display: none !important;
}

.mobile__menu ul li a {
  padding-left: 0 !important;
}

@media only screen and (min-width: 768px) {
  .mobile__menu {
    display: none !important;
  }
}
