body {
	background: $w;
	font-family: $lato; }

ul,
ol,
button {
	padding: 0;
	margin: 0; }

a {
	text-decoration: none;
	color: $main;
	&:hover {
		color: $context; } }

.b {
	font-weight: bold; }

.content {
	max-width: 1170px;
	margin: 0 auto;
	padding: 0 15px;
	@include r(1170) {
		max-width: 100%; } }

.topline {
	height: 80px;
	@include df;
	justify-content: space-between;
	align-items: center;
	color: $grey;
	font-size: 10px;
	padding: 0 15px;
	@include r(768) {
		height: 50px;
		background: $w;
		@include pf;
		top: 0;
		left: 0;
		right: 0;
		z-index: 99; }
	a {
		font-size: 10px;
		text-transform: uppercase;
		color: $grey;
		&:hover {
			color: $context; } }
	&__logo {
		img {
			display: block;
			width: 90px;
			height: 14px; } }
	&__l {
		width: 30%;
		@include r(768) {
			display: none; }
		&-menu {
			@include clr;
			li {
				float: left;
				margin-right: 30px;
				@include pr;
				&:before {
					content: '|';
					color: $grey;
					@include pa;
					right: -15px;
					top: -1px;
					@include r(510) {
						display: none; } }
				&:last-child {
					margin-right: 0;
					&:before {
						display: none; } }
				&.stores {
					svg {
						top: -2px; } }
				&.with--icon {
					a {
						padding-left: 25px; } }
				a {
					@include pr;
					&:hover {
						svg {
							fill: $context;
							path {
								fill: $context; } } } }
				svg {
					width: 13px;
					height: 16px;
					@include pa;
					left: 0; } } } }
	&__c {
		width: 40%;
		@include df;
		@include cc;
		@include r(940) {
			width: 30%; }
		@include r(768) {
			justify-content: flex-start; } }
	&__r {
		width: 30%;
		@include df;
		align-items: center;
		justify-content: flex-end;
		@include r(940) {
			width: 40%; }
		@include r(768) {
			width: 70%; }
		&-links {
			@include r(768) {
				display: none; }
			a {
				margin-right: 30px;
				@include pr;
				&:before {
					content: '|';
					color: $grey;
					@include pa;
					right: -15px;
					top: -1px;
					pointer-events: none;
					@include r(620) {
						display: none; } }
				&:last-child {
					margin-right: 0;
					&:before {
						display: none; } }
				span.b {
					color: $main; } } }
		&-buttons {
			margin-left: 35px;
			@include df;
			align-items: center;
			justify-content: flex-end;
			padding-right: 10px;
			button {
				background-color: transparent;
				border: 0;
				outline: none;
				cursor: pointer;
				margin-right: 22px;
				&:hover {
					svg {
						fill: $context;
						path {
							fill: $context; } } }
				&:last-child {
					margin-right: 0; }
				&.wishlist {
					width: 17px;
					height: 14px;
					svg {
						width: 17px;
						height: 14px; } }
				&.cart {
					@include pr;
					margin-right: 0;
					@include r(768) {
						margin-right: 22px; }
					&:hover {
						.cart__count {
							box-shadow: none;
							background: $context; } }
					svg {
						width: 14px;
						height: 16px; }
					.cart__count {
						width: 19px;
						height: 19px;
						border-radius: 90%;
						@include df;
						@include cc;
						@include pa;
						top: -10px;
						right: -10px;
						background: $main;
						color: $w;
						font-size: 10px;
						transition: box-shadow $t;
						box-shadow: 0 5px 15px rgba($b, 0.39); } } } } } }
.bottomline {
	height: 80px;
	@include df;
	@include cc;
	@include r(768) {
		margin-top: 50px; }
	&__menu {
		ul {
			@include df;
			@include cc;
			@include fw;
			li {
				margin-right: 57px;
				&:last-child {
					margin-right: 0; }
				a {
					text-transform: uppercase;
					font-size: 12px;
					font-weight: 900;
					line-height: 50px;
					display: block;
					@include pr;
					padding: 0 15px;
					&.active {
						&:before {
							@include c;
							@include pa;
							bottom: 0;
							left: 0;
							right: 0;
							height: 2px;
							background: #f3c0eb;
							opacity: 1; } } } } } } }

.text__right {
	text-align: right; }

.line {
	&:after {
		@include c;
		@include pa;
		top: 50px;
		z-index: -1;
		left: -15px;
		width: 155px;
		height: 17px; }
	&.line__pink {
		&:after {
			background: #f05c70; } }
	&.line__cream {
		&:after {
			background: #E9E4D1; } }
	&.line__greyblue {
		&:after {
			background: #9FA4BB; } }
	&.line__blue {
		&:after {
			background: #bdd3ff; } }
	&.line__dorange {
		&:after {
			background: #fe825d; } }
	&.line__orange {
		&:after {
			background: #ffd897; } }
	&.line__cian {
		&:after {
			background: #b2fbee; } }
	&.line__gcian {
		&:after {
			background: #96dcc6; } } }

.ttu {
	text-transform: uppercase; }

.mainslider {
	overflow: hidden;
	@include pr;
	.swiper-wrapper {
		z-index: 2; }
	&__pagination {
		@include df;
		@include cc;
		padding: 15px 0;
		@include pa;
		bottom: 100px !important;
		z-index: 2;
		width: auto !important;
		left: 50% !important;
		transform: translate(-50%, 0);
		span {
			font-size: 0;
			box-shadow: 0 0 0 2px transparent;
			width: 5px;
			height: 5px;
			background: $w;
			opacity: 1;
			outline: none;
			border-radius: 50%;
			&.swiper-pagination-bullet-active {
				box-shadow: 0 0 0 2px $w;
				background-color: transparent; } } }
	&__slide {
		@include pr; }
	&__item {
		min-height: 530px;
		width: 100%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		margin-bottom: 70px;
		@include pr;
		z-index: 1;
		&-blured {
			@extend .mainslider__item;
			@include pa;
			left: 50%;
			transform: translate(-50%, 0);
			z-index: 0; } }
	&__info {
		@include pa;
		z-index: 2;
		top: 115px;
		left: 95px;
		right: 95px;
		@include r(768) {
			text-align: center; }
		&.right {
			@include clr;
			.mainslider__title {
				text-align: right;
				@include r(768) {
					text-align: center; }
				&:after {
					left: auto;
					right: -35px;
					@include r(768) {
						right: auto;
						left: 50%;
						transform: translate(calc(-50% + -100px), 0) rotate3d(1, 0, 0, 180deg) !important; } } }
			.mainslider__link {
				float: right;
				@include r(768) {
					float: none; } } } }
	&__title {
		font-size: 70px;
		font-family: $old;
		color: $w;
		line-height: 80px;
		margin-bottom: 30px;
		&.dark {
			color: $main; }
		&:after {
			@include c;
			@include pa;
			top: 50px;
			z-index: -1;
			left: -35px;
			transition: transform 1s linear;
			transform: rotate3d(1, 0, 0, 180deg);
			@include r(768) {
				left: 50%;
				transform: translate(calc(-50% + -100px), 0) rotate3d(1, 0, 0, 180deg) !important; } } }
	&__link {
		font-size: 11px;
		color: $w;
		padding: 15px;
		display: inline-block;
		border: 1px solid rgba($w, .5);
		border-radius: 30px;
		&.dark {
			border-color: $main;
			color: $main;
			&:hover {
				color: $main; } }
		&:hover {
			color: $w; } }
	&__bullets {
		.first__bullet {
			left: calc(50% - 80px);
			top: 260px; }
		.second__bullet {
			right: calc(50% - 175px);
			bottom: 200px; } } }

.swiper-slide-active {
	.mainslider__title {
		&:after {
			transform: rotate3d(0, 0, 0, 0deg); } } }

.bullet {
	width: 37px;
	height: 37px;
	border-radius: 50%;
	@include df;
	@include cc;
	opacity: .2;
	transition: opacity $t;
	@include r(940) {
		display: none; }
	&:hover {
		opacity: 1; }
	&__item {
		@include pa;
		z-index: 3;
		&.is--top {
			z-index: 4; }
		&.first__bullet {
			.bullet__arrow {
				&:before {
					top: calc(50% - 45px); } } } }
	&__info {
		@include pa;
		min-width: 150px;
		padding: 15px;
		border-radius: 5px;
		background: $w;
		z-index: 3;
		top: -200px;
		left: 50px;
		box-shadow: 0 12px 22px rgba($b, 0.16);
		// opacity: 0
 }		// visibility: hidden
	&__text {
		z-index: 1;
		@include pr; }
	// &__close
	// 	+pa
	// 	top: 15px
	// 	right: 15px
	// 	width: 15px
	// 	height: 15px
	// 	border-radius: 50%
	// 	border: 0
	// 	background: $context
	// 	+df
	// 	+cc
	// 	z-index: 1
	// 	&-overlay
	// 		+pf
	// 		top: 0
	// 		bottom: 0
	// 		left: 0
	// 		right: 0
	// 		z-index: 2
	// 	&:before,
	// 	&:after
	// 		+c
	// 		+pa
	// 		width: 8px
	// 		height: 2px
	// 		background: $w
	// 	&:after
	// 		transform: rotate(45deg)
	// 	&:before
	// 		transform: rotate(-45deg)
	&__arrow {
		&:before {
			@include c;
			@include pa;
			top: 5px;
			left: -5px;
			@include arr(30, 30, $w, bl);
			transform: rotate(45deg); } }
	&:before {
		transform: rotate(90deg); }
	&:before,
	&:after {
		@include c;
		@include pa;
		background: $w;
		width: 2px;
		height: 10px; }
	&.bullet__pink {
		background: #f05c70; }
	&.bullet__white {
		background: $w;
		&:before,
		&:after {
			background: $main; } }
	&.bullet__dark {
		background: $main;
		&:before,
		&:after {
			background: $w; } } }

.cart {
	&.is--active {
		svg {
			fill: $context;
			path {
				fill: $context; } }
		.cart__count {
			background: $context !important;
			box-shadow: 0 5px 15px rgba($context, .39) !important; } }
	&__wrapper {
		@include pr; }
	&__popup {
		@include pa;
		right: 0;
		top: 30px;
		padding: 15px;
		z-index: 8;
		width: 285px;
		background: $w;
		opacity: 0;
		display: none;
		box-shadow: 0 22px 22px rgba(0,0,0,.16); }
	&__product {
		@include df;
		justify-content: space-between;
		@include pr;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0; }
		&-image {
			width: 30%;
			img {
				width: 100%; } }
		&-info {
			width: 70%;
			padding: 0 15px; }
		&-title,
		&-category {
			display: block;
			font-size: 12px; }
		&-title {
			font-weight: bold;
			color: $main !important; }
		&-category {
			margin: 9px 0 22px; }
		&-price {
			text-align: left !important; }
		&-remove {
			@include pa;
			width: 10px;
			height: 10px;
			top: 0;
			right: 0;
			svg {
				width: 100%;
				height: 100%; } } }
	&__sub {
		a {
			display: block; } }
	&__total {
		@include df;
		justify-content: space-between;
		font-size: 12px;
		color: $main !important;
		text-transform: uppercase;
		font-weight: bold;
		margin-bottom: 30px;
		align-items: center;
		&-l {
			width: 50%; }
		&-r {
			width: 50%;
			&-price {
				text-align: right !important; } } }
	&__checkout {
		max-width: 145px;
		text-align: center;
		margin: 0 auto 23px; }
	&__seebasket {
		text-align: center;
		text-transform: uppercase;
		font-size: 11px;
		color: $main !important; } }

.button {
	padding: 17px;
	border-radius: 32px;
	text-transform: uppercase;
	color: $w;
	font-size: 12px;
	background: #3e3e40;
	border: 0;
	outline: none;
	cursor: pointer;
	min-width: 100px;
	&.half__transparent {
		background: rgba(#3e3e40, .8); }
	&.transparent {
		background-color: transparent;
		padding: 0; }
	&.button__underline {
		text-decoration: underline;
		&:hover {
			text-decoration: none; } }
	&.dark__text {
		color: $main;
		&:hover {
			color: $context; } } }

.shop {
	&__item {
		@include pr;
		@include df;
		justify-content: center;
		flex-direction: column;
		flex-wrap: wrap;
		padding-bottom: 20px; }
	&__image {
		text-align: center; }
	&__title {
		text-align: center;
		text-transform: uppercase;
		font-weight: bold;
		font-size: 12px;
		margin-bottom: 15px; }
	&__price {
		text-align: center;
		font-weight: bold;
		font-size: 15px;
		text-transform: uppercase; }
	&__buy {
		@extend .button;
		@include pa;
		bottom: -40px;
		left: 50%;
		transform: translate(-50%, 0); } }

.section {
	&__title {
		@extend .mainslider__title;
		color: $main;
		text-transform: uppercase;
		font-size: 50px;
		line-height: 60px;
		margin-bottom: 0;
		&:after {
			top: 35px;
			left: -15px;
			@include r(668) {
				text-align: center;
				left: 50%;
				transform: translate(calc(-50% + -100px), 0); } } }
	&__headtitle {
		font-size: 16px;
		font-family: $old;
		text-transform: uppercase;
		font-weight: bold;
		@include pr;
		&:before {
			@include c;
			@include pa;
			top: -21px;
			left: 50%;
			transform: translate(-50%, 0);
			width: 52px;
			height: 8px;
			background: #fae498; } }
	&__text {
		line-height: 23px; } }

.style {
	&__image {
		width: 45%;
		height: 664px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		transition: box-shadow $t2;
		@include pr;
		@include r(940) {
			margin: 0 auto 30px;
			width: 50%; }
		@include r(768) {
			width: 100%; }
		&:hover {
			box-shadow: 0 12px 22px rgba($b, 0.16); }
		.first__bullet {
			top: 300px;
			right: 135px;
			.bullet__info {
				top: 35px;
				left: 35px; } } }
	&__info {
		width: 40%;
		margin-right: -75px;
		margin-top: -120px;
		@include pr;
		z-index: 1;
		@include r(940) {
			margin-top: 0;
			margin-right: 0;
			margin: 0 auto;
			width: 50%; }
		@include r(668) {
			width: 100%;
			text-align: center; } }
	&__description {
		margin-left: 15%;
		@include pr;
		margin-bottom: 70px;
		@include r(940) {
			margin-left: 0; } }
	&__item {
		width: 134px;
		height: 77px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		@include pr;
		&s {
			@include df;
			justify-content: space-between;
			@include r(940) {
				margin-bottom: 30px; } }
		.title {
			text-transform: uppercase;
			@include pa;
			bottom: 13px;
			left: 13px;
			font-weight: 900;
			font-size: 12px; } }
	.content {
		@include df;
		justify-content: flex-end;
		align-items: center;
		@include r(940) {
			display: block; } } }

.bold {
	font-weight: 900; }

.with--bullets {
	&:hover {
		.bullet {
			opacity: 1; } } }

.inspire {
	margin-top: -65px;
	@include r(940) {
		margin-top: auto; }
	&__item {
		width: 285px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		transition: box-shadow $t2;
		@include pr;
		@include r(1190) {
			width: 220px; }
		@include r(940) {
			width: 100%;
			margin-bottom: 30px; }
		&:hover {
			box-shadow: 0 12px 22px rgba($b, 0.16); }
		&s {
			height: 490px;
			@include df;
			@include fw;
			justify-content: space-between;
			width: 50%;
			@include r(1190) {
				justify-content: flex-end; }
			@include r(940) {
				display: block;
				height: auto;
				margin: 0 auto;
				order: 2; }
			@include r(768) {
				width: 100%; }
			&-left {
				@include df;
				flex-direction: column;
				justify-content: space-between;
				@include r(940) {
					display: block; } } }
		&.half {
			height: 48%;
			@include r(940) {
				height: 500px; }
			&:last-child {
				margin-bottom: 0;
				@include r(940) {
					margin-bottom: 30px; } }
			&:nth-of-type(1) {
				.first__bullet {
					top: 95px;
					right: 21px; } }
			&:nth-of-type(2) {
				.first__bullet {
					top: 137px;
					left: 23px; } } }
		&.full {
			height: 100%;
			@include r(940) {
				height: 500px; }
			.first__bullet {
				top: 195px;
				right: 121px; }
			.second__bullet {
				bottom: 30px;
				left: 95px; } } }
	&__info {
		@extend .style__info;
		margin-right: auto;
		margin-left: -75px;
		margin-top: -50px;
		width: 50%;
		@include r(940) {
			margin-top: auto;
			margin-left: 0;
			margin: 0 auto 30px;
			order: 1; }
		&-text {
			.button {
				margin-left: 100px;
				@include r(940) {
					margin-left: 0; } } } }
	.content {
		@include df;
		justify-content: flex-end;
		align-items: center;
		@include r(940) {
			flex-direction: column; } } }

.mostliked {
	padding: 100px 0;
	@include r(940) {
		padding: 50px 0; }
	&__headtitle {
		text-align: center;
 }		// margin-bottom: 51px
	&__item {
		width: 23%;
		@include pr;
		transition: box-shadow $t2;
		@include r(940) {
			width: 47%;
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0; } }
		@include r(768) {
			width: 100%; }
		&:hover {
			box-shadow: 0 12px 22px rgba($b, 0.16);
			.mostliked__colors {
				opacity: 1; }
			.mostliked__navigation {
				opacity: 1; }
			.mostliked__preview {
				opacity: 1;
				transform: translate(-50%, 0) rotate3d(0, 0, 0, 90deg); } }
		&s {
			overflow: hidden;
			padding: 50px 20px;
			@include df;
			@include fw;
			justify-content: space-between; } }
	&__navigation {
		@include pa;
		top: 50%;
		left: 0;
		right: 0;
		transform: translate(0, -50%);
		@include df;
		justify-content: space-between;
		z-index: 1;
		pointer-events: none;
		opacity: 0;
		transition: opacity $t2; }
	&__next,
	&__prev {
		pointer-events: all;
		background-color: transparent;
		outline: none;
		border: 0;
		cursor: pointer;
		padding: 15px;
		&:hover {
			svg {
				fill: $context;
				path {
					fill: $context; } } }
		svg {
			width: 8px;
			height: 12px; } }
	&__prev {
		transform: rotate(180deg); }
	&__image {
		@include pr;
		img {
			display: block;
			width: 100%; } }
	&__preview {
		@include pa;
